import React from "react"
import { Link } from 'react-router-dom';
import Breadcrumbs from '@material-ui/core/Breadcrumbs';

export default class CreateProduct extends React.Component {
    render() {
        return (
            <div className='help-page'>
                <Breadcrumbs aria-label="breadcrumb">
                    <Link to="/help">
                        Help Center
                    </Link>
                    <Link to="/help/projectDetails">
                        Uploading
                    </Link>
                </Breadcrumbs>
                <h1>Create a new project and import a model file</h1>
                <h2>Create a new project</h2>
                <ol>
                    <li>
                        Click on the new project link on the navigation bar or click the new project button in the product manager.
                    </li>
                    <li>
                        Enter a name and description for the project.
                    </li>
                    <li>
                        Select or drag and drop a single self-contained model file or folder if your model has extra asset files such textures.
                    </li>
                    <li>
                        Click the submit button. The model file will be uploaded and converted to an optimised file format.
                    </li>
                    <li>
                        Your project is now ready to be edited in Ezyvue Studio.
                    </li>
                </ol>
                The following 3d model file formats are supported:<br />
                <div>
                    <br />
                    <ul>
                        <li>OBJ (Wavefront)</li>
                        <li>FBX (Filmbox)</li>
                        <li>glTF (OpenGL Transmission Format) as .glb or .gltf files</li>
                    </ul>
                    <br />
                </div>
                All files are automatically converted to optimized binary glTF (.glb) and USDZ (Apple Augmented Reality format).
                These can be exported on the manage asset page.
                <br/>
                <br/>
                <h2>Re-uploading a model file</h2>
                <p>
                    If you make external changes to your model you can upload the model by clicking the
                    re-upload button in the Model Files section of the Manage Project page.
                </p>
                <em>
                    Note: if you re-upload your model you may lose changes made in Ezyvue Studio.
                </em>
                <br/>
                <h2>Troubleshooting</h2>
                <ol>
                    <li>In case of a network error check your connection and try again.</li>
                    <li>There is a single upload limit 50 MB of data. Ensure you&apos;re not exceeding this.</li>
                    <li>If you exported or converted the model try a different image package or converter.</li>
                    <li>Clear your browser cache. Close the browser and re-open again.</li>
                    <li>Check that you selected the correct model directory and included all the necessary assets such as textures.</li>
                    <li>Check the model file name does not contain invalid whitespace characters.</li>
                    <li>Check your model file is valid. On windows double-clicking on the model should open the model viewer. Ensure that it loads correctly.</li>
                    <li>If you are still unable to upload your model files please contact us and share the model file using DropBox or other online storage service.</li>
                </ol>
            </div>
        );
    }
}
